	}
}

.calculate__title_other {
	margin-bottom: 20px;

	@media (min-width : 768px) {
		margin-bottom: 30px;
	}
}


/*Селект для переключения калькулятора*/
.calculate__select {
	width: 139px;
	margin: 0 auto;
	margin-bottom: 23px;
	position: relative;
	z-index: 2;

	@media (min-width : 768px) {
		display: flex;
		justify-content: center;
		margin-bottom: 31px;
	}

	@media (min-width : 1200px) {
		margin: 0;
		margin-left: 63px;
	}
}

.calculate__select .selectric{
	background-color: #ffffff;
	border-radius: 20px;
	border: 1px solid #ffffff;

	color: #000000;
	font-size: 13px;
	text-transform: uppercase;
	font-weight: 900;
	font-family: "Optima";

	span {
		display: flex;
		justify-content: center;
		align-items: center;

		line-height: 9px;
		font: inherit;
	}

	@media (min-width : 768px) {
		font-size: 14px;
		width: 191px;
	}

	@media (min-width : 1200px) {
		font-size: 10px;
		width: 144px;
		font-size: 16px;
	}
}

.calculate__select .button {
	background-color: #ffffff;

	&::after {
		border: none;

		background-image: url('../img/arrow-down.png');
		width: 18px;
		height: 15px;
		top: 7px;
		left: -40px;


		@media (min-width : 1200px) {
			left: -15px;
		}

	}

}

.calculate-up.up-title {
	margin-top: 0;

	@media (min-width : 1200px) {
		margin-top: -95px;
	}
}

.calculate-up {
	margin-top: 40px;

	@media (min-width : 1200px) {
		margin-top: 0px;
	}
}

.calculate-up.up-title {
	&::before {
		transform: translate(-50%, 50px);

		@media (min-width : 768px) {
			transform: translate(-50%, 60px);
		}

		@media (min-width : 1920px) {
			width: 580px;
			height: 580px;
			transform: translate(-50%, 30px);
		}
	}
}


/*Заголовок верхнего блока*/
.calculate-up {
	position: relative;

	@media (min-width : 768px) {
		background-size: 419px 419px;
	}

	&::before {
		content: '';

		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-50%, 110px);

		background-color: rgba(95, 95, 74, 0.82);
		/*background-color: red;*/
		border-radius: 50%;

		width: 250px;
		height: 250px;

		opacity: 0.72;
		z-index: 1;

		@media (min-width : 768px) {
			width: 419px;
			height: 419px;

			transform: translate(-50%, 130px);
		}

		@media (min-width : 1200px) {
			transform: translate(-50%, 60px);
		}

		@media (min-width : 1920px) {
			width: 580px;
			height: 580px;
			transform: translate(-50%, 30px);
		}
	}
}

